.Restore:hover {
    background: rgb(255, 63, 63);    
}
.Restore:active {
    -webkit-transform: translate(1px, 1px) !important;
        -ms-transform: translate(1px, 1px) !important;
            transform: translate(1px, 1px) !important;
}
.ToolTip{
    position: relative;
    height: 50px;
}

.Attempts_1::after{content: '1';}
.Attempts_2::after{content: '2';}
.Attempts_3::after{content: '3';}
.Attempts_0::after{content: '0';}
.Attempts_4::after{content: '3';}

.Attempts_0, .Attempts_4{
    cursor: not-allowed;
    opacity: 0.50;
}

.Attempts_0::after, 
.Attempts_1::after, 
.Attempts_2::after, 
.Attempts_3::after, 
.Attempts_4::after{
    position: absolute;
    left: 32px;
    font-size: 13px;
    display: block;
    width: 5px;
    height: 10px;
    top:0;
    background: #008cff98;
    padding: 2px  6px 6px 6px;
    border-radius: 0 0 0 10px;
    z-index: 305;
}

.ToolTipText{
    display: inline-block;
    visibility: hidden;
    width: 120px;
    padding: 15px;
    border: 1px solid #161616;
    -webkit-box-shadow: 5px 5px 25px 0 #0000004b;
            box-shadow: 5px 5px 25px 0 #0000004b;
    color:rgb(255, 193, 37);
    background: rgba(32, 32, 32, 0.7);
    text-align: center;
    border-radius: 0 6px 6px 0;
    position: absolute;
    z-index: 300;
    top: 0%;
    left: 100%;
    opacity: 0;
    -webkit-transition: opacity 0.3s;
    -o-transition: opacity 0.3s;
    transition: opacity 0.3s;
}

.Clicked svg{
    stroke:#4fb0ff;
    fill:#4fb0ff;
}

.Clicked {
    -webkit-transform: translate(-2px,-2px);
        -ms-transform: translate(-2px,-2px);
            transform: translate(-2px,-2px);
    -webkit-box-shadow: 2px 2px 5px rgb(52, 255, 255);
            box-shadow: 2px 2px 5px rgb(52, 255, 255);
}

.Clicked:hover {
    -webkit-transform: translate(-2px,-2px);
        -ms-transform: translate(-2px,-2px);
            transform: translate(-2px,-2px);
    -webkit-box-shadow: 2px 2px 5px rgb(52, 255, 255) !important;
            box-shadow: 2px 2px 5px rgb(52, 255, 255) !important;
}

.ToolTip:hover .ToolTipText{
    visibility: visible;
    opacity: 1;
}


