.GameMenu{  
  float: right;
  width: 50px;
  background: #303030cb;
  -webkit-box-shadow: 2px 2px 1px rgba(7, 7, 7, 0.555);
          box-shadow: 2px 2px 1px rgba(7, 7, 7, 0.555);
  border-radius: 5px;
  margin-top: 10px;
  margin-right: 5px;
}

.GameMenu ul {
   margin: 0;
   padding: 0;
}

.GameMenu ul li {
    display: block;
    clear: both;
    list-style: none;
    margin: 0;
    padding: 0;
    color: rgb(253, 253, 253); 
    stroke:none;
    fill:#ffffff;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
 }
 .GameMenu ul li svg:active {
    stroke:none;
    fill:#ffffff;
 }
 .GameMenu ul li:hover {
    -webkit-transform: translate(-2px,-2px);
        -ms-transform: translate(-2px,-2px);
            transform: translate(-2px,-2px);
    -webkit-box-shadow: 2px 2px 5px rgb(0, 0, 0);
            box-shadow: 2px 2px 5px rgb(0, 0, 0);
}




