.Remove{
    cursor: pointer;
    position: relative;
}
.Remove:hover {
    background: rgb(220, 44, 255);
}
.Remove:active {
    -webkit-transform: translate(1px, 1px) !important;
        -ms-transform: translate(1px, 1px) !important;
            transform: translate(1px, 1px) !important;
}
.Attempts_0::after{ content: '0';}
.Attempts_1::after{ content: '1';}
.Attempts_2::after{ content: '2';}
.Attempts_3::after{ content: '3';}
.Attempts_4::after{ content: '3';}

.Attempts_0, .Attempts_4{
    cursor: not-allowed !important;
    opacity: 0.50;
}

.Attempts_0::after, 
.Attempts_1::after, 
.Attempts_2::after, 
.Attempts_3::after, 
.Attempts_4::after{
    position: absolute;
    left: 32px;
    color: white;
    font-size: 13px;
    display: block;
    width: 5px;
    height: 10px;
    top:0;
    background: #008cff98;
    padding: 2px  6px 6px 6px;
    border-radius: 0 0 0 10px;
    z-index: 305;
}

.ToolTipText{
    display: inline-block;
    visibility: hidden;
    width: 120px;
    padding: 18px;
    border: 1px solid #161616;
    -webkit-box-shadow: 5px 5px 25px 0 #0000004b;
            box-shadow: 5px 5px 25px 0 #0000004b;
    background: rgba(32, 32, 32, 0.7);
    color:rgb(255, 209, 93);
    text-align: center;
    border-radius: 0 6px 6px 0;
    position: absolute;
    z-index: 300;
    bottom: 0%;
    left: 100%;
    opacity: 0;
    -webkit-transition: opacity 0.3s;
    -o-transition: opacity 0.3s;
    transition: opacity 0.3s;
}
.Clicked svg{
    stroke:rgb(255, 255, 255);
    fill:rgb(255, 255, 255);
}

.Clicked {
    -webkit-transform: translate(-2px,-2px);
        -ms-transform: translate(-2px,-2px);
            transform: translate(-2px,-2px);
    -webkit-box-shadow: 2px 2px 5px rgb(248, 59, 255);
            box-shadow: 2px 2px 5px rgb(248, 59, 255);
}

.Remove:hover .ToolTipText{
    cursor: default;
    visibility: visible;
    opacity: 1;
}


